.banner {
    background: rgb(44, 50, 55);
    height: 450px;
}

.banner_content {
    display: flex;
    flex-direction: column;
}

.banner_label {
    text-align: center;
    font-size: calc(0.3rem + 5vw);
    color: #ffffff;
    margin-top: 70px;
    font-family: Metropolis,sans-serif;
    font-weight: 500;
}

.banner_label_button_part {
    margin-top: 0;
}

.banner_buttons {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    padding-top: 30px;
    padding-bottom: 20px;
    margin: 0 auto;
    max-width: 450px;
}

.banner_button {
    margin: 5px 10px;
    padding: 15px 30px;
    border: 2px #ffffff solid;
    color: white;
    z-index: 0;
    text-decoration: none;
    cursor: pointer;
    font-size: .8em;
    font-family: 'Open Sans', sans-serif;
    font-weight: 700;

}

.banner_button:hover {
    background-color: #ffffff;
    color: #1b1f23;
    transition: all 600ms ease;
}

@media screen and  (max-width: 1100px) and  (min-width: 500px) {

    .banner {
        font-family: Metropolis,sans-serif;
        background: rgb(44, 50, 55);
        height: 350px;
    }

}